.header
{
    background-color: #090B12;
    position: fixed;
    height: 80px;
    width: 100%;
    z-index: 1000099;
}
  
.header .header-wrapper
{
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-color: #ffffff1a;
    height: 80px;
    position: relative;
}

.header .component
{
    height: 40px !important;
    left: 39px !important;
    position: absolute !important;
    top: 19px !important;
    width: 40px !important;
}

.header .header-right
{
    align-items: center;
    display: inline-flex;
    gap: 24px;
    left: 1665px;
    position: absolute;
    top: 19px;
}
  
.frame
{
    align-items: center;
    background-color: #2985D9;
    border-radius: 12px;
    display: inline-flex;
    flex-direction: column;
    gap: 10px;
    padding: 8px 16px;
    position: relative;
}

